.modal-terminal {
  z-index: 1000;
  background-color: #fff;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 17vh solid #000c;
  border-width: 17vh 17vw;
  width: 66vw;
  height: 66vh;
  margin: -17vh -17vw;
  position: fixed;
  top: 17vh;
  left: 17vw;
  overflow: auto;
}

.modal-terminal button {
  z-index: 1001;
  position: fixed;
  top: 17vh;
  right: 17vw;
}

.modal-terminal pre, .modal-terminal button {
  margin: 5px;
}

pre code.hljs {
  padding: 1em;
  display: block;
  overflow-x: auto;
}

code.hljs {
  padding: 3px 5px;
}

.hljs {
  color: #444;
  background: #f3f3f3;
}

.hljs-comment {
  color: #697070;
}

.hljs-tag, .hljs-punctuation {
  color: #444a;
}

.hljs-tag .hljs-name, .hljs-tag .hljs-attr {
  color: #444;
}

.hljs-keyword, .hljs-attribute, .hljs-selector-tag, .hljs-meta .hljs-keyword, .hljs-doctag, .hljs-name {
  font-weight: bold;
}

.hljs-type, .hljs-string, .hljs-number, .hljs-selector-id, .hljs-selector-class, .hljs-quote, .hljs-template-tag, .hljs-deletion {
  color: #800;
}

.hljs-title, .hljs-section {
  color: #800;
  font-weight: bold;
}

.hljs-regexp, .hljs-symbol, .hljs-variable, .hljs-template-variable, .hljs-link, .hljs-selector-attr, .hljs-operator, .hljs-selector-pseudo {
  color: #ab5656;
}

.hljs-literal {
  color: #695;
}

.hljs-built_in, .hljs-bullet, .hljs-code, .hljs-addition {
  color: #397300;
}

.hljs-meta {
  color: #1f7199;
}

.hljs-meta .hljs-string {
  color: #38a;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}
